import React from 'react';
import { Div, Search, Cell } from '@vkontakte/vkui';
// import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
// import Icon24Back from '@vkontakte/icons/dist/24/back';
// import ModalProduct from '../ModalProduct.js';
import OneProduct from "./OneProduct";

import axios from "axios";
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
require('../../panels/global/index.js');

class ProductList extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activePanel: 'panel1',
            nameCategory: 'none',
            productsLists: null,
            products:[{"1":{"title":"ЕКАРНЫЙ БАБАЙ"}}]
        }

    }

    render() {

        function ReverceCatalog(Pr){
            var res = Object.keys(Pr).reduceRight(function (arr, last, index, coll) {
                // console.log(last, index);
                return (arr = arr.concat(last))
            }, []);
            return res;
        }

        function SearchProductTitle(Pr,DSr){
            //console.log(Pr);

            const Objs = Object.keys(Pr);

            const retr = [];

           Objs.forEach(function (oneProduct, keyId, array) {
               if(testInput(DSr, Pr[oneProduct][0]['title'])){
                   retr[keyId] = oneProduct;
                  // console.log(Pr[oneProduct][0]);
               }
                }
            );
            return retr;
        }


        function SearchProductDescription(Pr,DSr){
            //console.log(Pr);

            const Objs = Object.keys(Pr);

            const retr = [];

           Objs.forEach(function (oneProduct, keyId, array) {
               if(testInput(DSr, Pr[oneProduct][0]['description'])){
                   retr[keyId] = oneProduct;
               }
                }
            );
            return retr;
        }


        function SearchProductType(Pr,DSr,ListIdProduct){
            const retr = [];

            ListIdProduct.forEach(function (oneProduct, keyId, array) {
               if(testType(DSr, Pr[oneProduct][0]['modyficate'])){
                   retr[keyId] = oneProduct;
               }
           }
            );
            return retr;
        }

        function testType(re,modyficate) {
            var status = false;
            if(modyficate.length <=0){
                status = false;
            }else{
                modyficate.forEach(function(oneProduct, keyId, array){
                    if(oneProduct.mod === re){
                        status = true;
                    }
                });
            }

            return status;
        }

        function testInput(re, str) {
            re = re.toLowerCase().replace(/ /g,"_").replace(/-/g,"_");
            str = str.toLowerCase().replace(/ /g,"_").replace(/-/g,"_");

            var midstring;
            if (str.search(re) != -1) {
                midstring = true;
            } else {
                midstring = false;
            }

            return midstring;
        }

        function compare(a, b) {
            if (a.price < b.price) {
                return -1;
            }
            if (a.price > b.price) {
                return 1;
            }
            // a должно быть равным b
            return 0;
        }

        function comparePrice(Pr){
            //console.log(Pr);
            return Object.keys(Pr).sort(compare)
        }


        function ArrayProducts(Pr,ClickProductF,setProductModal,setCart, getButtonInCartProduct,ReverceStatus = 0,SearchText = null) {
            var CatalogLists = [];
            if(ReverceStatus === 0){
                CatalogLists = ReverceCatalog(Pr);
            }
            if(ReverceStatus === 1){
                CatalogLists = comparePrice(Pr);
            }

            if(ReverceStatus === 2){
                CatalogLists = Object.keys(Pr);
            }

            if(ReverceStatus === 3){
                if(global.settingsFilterSearch.typeSearch === 0) {
                    CatalogLists = SearchProductTitle(Pr, SearchText);
                }else{
                    CatalogLists = SearchProductDescription(Pr, SearchText);
                }
            }

            if(global.settingsFilterSearch.typeProduct === false){}else{
                CatalogLists = SearchProductType(Pr, global.settingsFilterSearch.typeProduct,CatalogLists);
            }


            //console.log('|||||||||||||||||||||||||||||||',CatalogLists.length);

            const ArrayPr = CatalogLists.map((oneProduct, keyId) =>
                <OneProduct key={'Products_'+keyId} product={Pr[oneProduct]} ClickProduct={ClickProductF.bind(this)} setCart={setCart.bind(this)} setProductModal={setProductModal.bind(this)} getButtonInCartProduct={getButtonInCartProduct.bind(this)}/>
            );
            return ArrayPr;
        }


        function ShowProductCategory(nameCategorys){
            //console.log('ShowProductCategory ',nameCategorys);
            if(nameCategorys.nameCategorys === 'sets'){
                // console.log('d-d-d-d-',nameCategorys.productsLists[nameCategorys.idCategory]);
                return (<GetSets
                    SearchState={nameCategorys.SearchState}
                    key={nameCategorys.key}
                    names={nameCategorys.nameCategorys}
                    ProductArray={nameCategorys.productsLists}
                    ClickProduct={nameCategorys.ClickProduct.bind(this)}
                    setCart={nameCategorys.setCart.bind(this)}
                    setProductModal={nameCategorys.setProductModal.bind(this)}
                    getButtonInCartProduct={nameCategorys.getButtonInCartProduct.bind(this)}
                />);
            }
            if(nameCategorys.nameCategorys === 'sushi'){
                return (<GetSushi names={nameCategorys.nameCategorys}/>);
            }
            if(nameCategorys.nameCategorys === 'roli'){
                return (<GetRoli names={nameCategorys.nameCategorys}/>);
            }else{
                return (<GetNoneCat names={nameCategorys.nameCategorys}/>);
            }

        }


        function GetSets(ArrayPr) {
           //console.log('ArrayPr ',ArrayPr);
            if(ArrayPr.SearchState == '' || ArrayPr.SearchState === false){
                return (
                    <Div id={"block_categorys_"+ArrayPr.names} key={"block_categorys_"+ArrayPr.names} className="blockCategoryFlex">
                        {ArrayProducts(ArrayPr.ProductArray,ArrayPr.ClickProduct.bind(this),ArrayPr.setProductModal.bind(this),ArrayPr.setCart.bind(this),ArrayPr.getButtonInCartProduct.bind(this),0)}
                    </Div>
                );
            }else{
                return (
                    <Div id={"block_categorys_"+ArrayPr.names} key={"block_categorys_"+ArrayPr.names} className="blockCategoryFlex">
                        {ArrayProducts(ArrayPr.ProductArray,ArrayPr.ClickProduct.bind(this),ArrayPr.setProductModal.bind(this),ArrayPr.setCart.bind(this),ArrayPr.getButtonInCartProduct.bind(this),3,ArrayPr.SearchState)}
                    </Div>
                );
            }
        }


        function SearchFn(ArrayPr,SrText) {
           //console.log('ArrayPr ',ArrayPr);
            return (
                <Div id={"block_categorys_"+ArrayPr.names} key={"block_categorys_"+ArrayPr.names} className="blockCategoryFlex">
                    {ArrayProducts(ArrayPr.ProductArray,ArrayPr.ClickProduct.bind(this),ArrayPr.setProductModal.bind(this),ArrayPr.setCart.bind(this),ArrayPr.getButtonInCartProduct.bind(this),3,SrText)}
                </Div>
            );
        }

        function GetSushi(names) {
            return (
                <Div id={names.names} className="blockCategoryFlex">
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Суши" src="https://sushi-time31.ru/images/tovars/croppedImg_93445642.png"/></Div><Div className="titleProduct">Суши с креветкой</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig">Стоимость <span className="priceProduct">1 000 руб.</span></Div></Div>
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Суши" src="https://sushi-time31.ru/images/tovars/croppedImg_93445642.png"/></Div><Div className="titleProduct">Суши с лососем</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig">Стоимость <span className="priceProduct">1 000 руб.</span></Div></Div>
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Суши" src="https://sushi-time31.ru/images/tovars/croppedImg_93445642.png"/></Div><Div className="titleProduct">Суши краб</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig">Стоимость <span className="priceProduct">1 000 руб.</span></Div></Div>
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Суши" src="https://sushi-time31.ru/images/tovars/croppedImg_93445642.png"/></Div><Div className="titleProduct">Суши копченый лосот</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig">Стоимость <span className="priceProduct">1 000 руб.</span></Div></Div>
                </Div>
            );
        }

        function GetRoli(names) {
            return (
                <Div id={names.names} className="blockCategoryFlex">
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Роллы" src="https://sushi-time31.ru/images/tovars/croppedImg_1061187804.jpeg"/></Div><Div className="titleProduct">Ролл с тунцом</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig"> <span className="priceProduct">1 000 руб.</span></Div></Div>
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Роллы" src="https://sushi-time31.ru/images/tovars/croppedImg_1061187804.jpeg"/></Div><Div className="titleProduct">Ролл с лососем</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig"> <span className="priceProduct">1 000 руб.</span></Div></Div>
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Роллы" src="https://sushi-time31.ru/images/tovars/croppedImg_1061187804.jpeg"/></Div><Div className="titleProduct">Ролл Филадельфия</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig"> <span className="priceProduct">1 000 руб.</span></Div></Div>
                    <Div className="blockProduct"><Div className="imgPrewieProduct"><img alt="Роллы" src="https://sushi-time31.ru/images/tovars/croppedImg_1061187804.jpeg"/></Div><Div className="titleProduct">Филадельфия с крабом</Div><Div className="DescrProduct">Описание товара</Div><Div className="priceProductBig"> <span className="priceProduct">1 000 руб.</span></Div></Div>
                </Div>
            );
        }

        function GetNoneCat(names){
            return (
                <Div id={names} className="blockCategoryFlex">
                <h1>К сожаление категория пуста!</h1>
                </Div>
            );
        }

        // function GetModalProduct(product){
        //     return(
        //
        //     )
        // }

        return (
            <div>
                {/*<Cell*/}
                {/*    className="BoxCellSearch"*/}
                {/*    asideContent={*/}
                {/*        <Icon24Filter*/}
                {/*            onClick={*/}
                {/*                ()=>{this.props.showModal('MODAL_Filter_Catalog');}*/}
                {/*            }*/}
                {/*        />}*/}
                {/*>*/}
                {/*<Search*/}
                {/*    value={this.state.search}*/}
                {/*    onChange={this.onChange}*/}
                {/*    after={null}*/}
                {/*/>*/}
                {/*</Cell>*/}
                <div className="clrHeight"></div>
            <ShowProductCategory
                SearchState={this.props.searchProduct}
                productsLists={this.props.productsLists}
                nameCategorys={this.props.nameCategory}
                idCategory={this.props.idCategorys}
                ClickProduct={this.props.onClickProduct.bind(this,'Panels_Product')}
                setCart={this.props.setCart.bind(this)}
                setProductModal={this.props.setProductModal.bind(this)}
                getButtonInCartProduct={this.props.getButtonInCartProduct.bind(this)}
            />
            </div>
        );
    }
}

export default ProductList;